/* ------------------------------------------------ */

export const exportToExcel = (tableId) => {
  let d = new Date();
  const table = document.getElementById(tableId);
  const ws = XLSX.utils.table_to_sheet(table);  // Convert the HTML table to a worksheet

  // Create a workbook with a single worksheet
  const wb = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(wb, ws, "Sheet1");

  // Save the workbook as an Excel file
  XLSX.writeFile(wb, `${d.getTime()}_${tableId}.xlsx`, {
    bookType: "xlsx",
    bookSST: false,
    type: "binary",
    raw: true,
  });
};

/* ------------------------------------------------ */

export const getCookieValue = (cookieName) => {
  const name = cookieName + "=";
  const decodedCookie = decodeURIComponent(document.cookie);
  const cookieArray = decodedCookie.split(';');
  for (let i = 0; i < cookieArray.length; i++) {
    let cookie = cookieArray[i];
    while (cookie.charAt(0) === ' ') {
      cookie = cookie.substring(1);
    }
    if (cookie.indexOf(name) === 0) {
      return cookie.substring(name.length, cookie.length);
    }
  }
  return null; // Cookienotfound
}

export const checkCookieExists = (cookieName) => {
  const cookies = document.cookie.split(";");
  for (let i = 0; i < cookies.length; i++) {
    const cookie = cookies[i].trim();
    if (cookie.indexOf(cookieName + "=") === 0) {
      return true;
    }
  }
  return false;
};

