import React, {useEffect, useState} from "react";
import axios from "axios";
import {errorToast, ipAddress, successToast, getCookieValue, tokenName} from "../../utils/Common";
import Swal from "sweetalert2";
import {useForm} from "react-hook-form";
import {CCardHeader} from "@coreui/react";
import Button from "react-bootstrap/Button";
import Table from "react-bootstrap/Table";
import {Link} from "react-router-dom";
import Modal from "react-bootstrap/Modal";

const AdminManageCountries = () => {

  let [countries, setCountries] = useState([]);

  function viewAllCountries() {
    // console.log("In View All Countries");
    let token = getCookieValue(tokenName);
    axios.get(ipAddress + "admin/viewCountries", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    })
      .then((response) => {
        // localStorage.setItem("recordset", JSON.stringify(response.data.recordset));
        console.log(response.data);
        // console.log(response.data.recordset);
        setCountries(response.data.recordset);
      }).catch(e => {
      console.log(e)
    })
  }


  useEffect(() => {
    // callLocalStorage();
    viewAllCountries();
  }, []);


  // Model popup form function
  function onSubmit(Param) {
    let token = getCookieValue(tokenName);

    axios.post(ipAddress + "admin/insertCountry", Param, {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    }).then((response) => {
      console.log(response.data.error);
      if (response.data.error === false) {
        successToast(response.data.message);
        viewAllCountries();
      } else {
        errorToast("Error while Adding");
      }

    }).catch(e => {
      console.log(e)
    })
  }

  function deleteCountry(countryid) {
    let token = getCookieValue(tokenName);
    Swal.fire({
      title: "Are you sure?",
      text: "You won't be able to revert this!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!"
    }).then((result) => {
      if (result.isConfirmed) {

        // title: "Deleted!",
        // text: "Your file has been deleted.",
        // icon: "success"

        axios.delete(ipAddress + "admin/deleteCountry/" + countryid, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content_Type': 'application-json',
          }
        })
          .then((response) => {
            console.log(response.data.error);
            if (response.data.error === false) {
              successToast(response.data.message);
              viewAllCountries();
            } else {
              errorToast("Error while Deleting");
            }

          }).catch(e => {
          console.log(e)
        })

      }
    });


  }

  // function callLocalStorage() {
  //   let recordset = JSON.parse(localStorage.getItem("recordset"));
  //   setCountries(recordset);
  // }

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => {
    setShow(true);
  }

  const {
    register,
    handleSubmit,
    formState: {errors}
  } = useForm();

  return (
    <>
      <CCardHeader><strong>Manage Countries </strong> </CCardHeader>
      <br/>
      <Button variant="primary" onClick={handleShow}>
        Add Country
      </Button>
      <br/>
      <br/>
      <div style={{height: "330px"}} className="table-responsive">
        <Table responsive hover>
          <thead style={{position: "sticky", top: 0}}>
          <tr>
            <th> Country ID</th>
            <th> Country Name</th>
            <th> Code</th>
            <th> Region Code</th>
            <th> Currency Code</th>
            <th> Delete Country</th>
            <th> Edit Info</th>
          </tr>
          </thead>

          <tbody>
          {countries?.length > 0 && countries.map((item, index) => (
            <tr key={index}>
              <td>{item.CountryId}</td>
              <td>{item.Name}</td>
              <td><span className="badge bg-info ms-auto"> {item.Code} </span></td>
              <td>{item.RegionCode}</td>
              <td className="text-center">{item.CurrencyCode}</td>
              <td className="text-center"><Button variant="danger" size={"sm"} style={{color: 'white'}}
                                                  onClick={() => deleteCountry(item.CountryId)}>Delete</Button>
              </td>
              {/*<td className="text-center"><Button variant="success"  >Edit</Button>*/}
              <td className="text-center">
                <Link to="/edit-country" state={{data: item}} className="btn btn-success btn-sm"
                      style={{color: 'white'}}>Edit</Link>
              </td>
            </tr>
          ))
          }
          </tbody>
        </Table>
      </div>

      <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add New Country</Modal.Title>
        </Modal.Header>
        <Modal.Body>

          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="mb-3">
              <label htmlFor="name">Country Name</label>
              <input {...register('name', {required: true})} placeholder="Country Name" type="text" id="name"
                     className="form-control"/>

              {errors.name && <p className='text-danger'>This is a required field.</p>}
            </div>

            <div className="mb-3">
              <label htmlFor="code">Country Code</label>
              <input {...register('code', {required: true})} maxLength={5} placeholder="Country Code" type="text"
                     id="code"
                     className="form-control"/>

              {errors.code && <p className='text-danger'>This is a required field.</p>}
            </div>

            <div className="mb-3">
              <label htmlFor="region">Region Code</label>
              <input {...register('region', {required: true})} placeholder="Region Code" type="text" id="region"
                     className="form-control"/>

              {errors.region && <p className='text-danger'>This is a required field.</p>}
            </div>

            <div className="mb-3">
              <label htmlFor="currency">Currency Code</label>
              <input {...register('currency', {required: true})} placeholder="Currency Code" type="text"
                     id="currency"
                     className="form-control"/>

              {errors.currency && <p className='text-danger'>This is a required field.</p>}
            </div>

            <hr/>

            <div className="mb d-flex justify-content-between align-items-center">
              <Button variant="secondary" onClick={handleClose}>
                Close
              </Button>

              <Button variant="primary" type="submit" className="ml-auto">
                Add Country
              </Button>
            </div>


          </form>

        </Modal.Body>

        {/*<Modal.Footer>*/}
        {/*  <Button variant="secondary" onClick={handleClose}>*/}
        {/*    Close*/}
        {/*  </Button>*/}
        {/*  <Button variant="primary" onSubmit={handleSubmit(onSubmit)} >*/}
        {/*    Save Changes*/}
        {/*  </Button>*/}
        {/*</Modal.Footer>*/}

      </Modal>
    </>
  )
}

export default AdminManageCountries
