import React, {Component, Suspense} from 'react'
import {HashRouter, Route, Routes} from 'react-router-dom'
import './scss/style.scss'

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

import 'react-toastify/dist/ReactToastify.css';
import AdminManageCountries from "./views/dashboard/AdminManageCountries";

// Containers
const DefaultLayout = React.lazy(() => import('./layout/DefaultLayout'))

// Pages
const Login = React.lazy(() => import('./views/pages/login/Login'))
const Register = React.lazy(() => import('./views/pages/register/Register'))
const Page404 = React.lazy(() => import('./views/pages/page404/Page404'))
const Page500 = React.lazy(() => import('./views/pages/page500/Page500'))

// import Dashboard from "./views/dashboard/Dashboard"

class App extends Component {
  render() {
    return (
      <HashRouter>
        <Suspense fallback={loading}>
          <Routes>
            <Route exact path="/" name="Login Page" element={<Register/>}/>
            {/*<Route exact path="/newmidofficelogin" name="Login Page" element={<Register/>}/>*/}
            {/*<Route exact path="/adminmanagecountries" name="Admin Manage Countries" element={<AdminManageCountries />} />*/}
            <Route exact path="/404" name="Page 404" element={<Page404/>}/>
            <Route exact path="/500" name="Page 500" element={<Page500/>}/>
            <Route path="*" name="Home" element={<DefaultLayout/>}/>
          </Routes>
        </Suspense>
      </HashRouter>
    )
  }
}

export default App
